<template>
  <div class="container">
    <div class="data">
      <div class="title">
        <div class="desc">车队协议配置</div>
      </div>
      <div class="table">
        <el-table style="width: 100%" :data="showData"  stripe border  show-summary :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in showColumn"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
          </el-table-column>
        </el-table>
        <el-table style="width: 100%" :data="editData"  stripe border  show-summary :summary-method="getSummaries" :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in editColumn"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
            <template slot-scope="scope">
              <el-input v-if="edits.indexOf(column.prop)!==-1"
                        v-model="scope.row[column.prop]" size="small">
              </el-input>
              <span v-else>{{scope.row[column.prop]}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-row>
        <el-col :span="12">
          <div class="approval">
            <div class="result">
              <div class="title">申报情况说明</div>
              <el-input type="textarea" :rows="5" v-model="approve1"></el-input>
            </div>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="approval">
            <div class="result">
              <div class="title">申报情况说明</div>
              <el-input type="textarea" :rows="5" v-model="approve2"></el-input>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="option">
      <back-button>上一步</back-button>
      <back-button type="next" @next="collect">保&nbsp;&nbsp;存</back-button>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BackButton = () => import('@/components/BackButton')
export default {
  name: 'configProtocol',
  components: {BackButton},
  computed: {
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  data () {
    return {
      showColumn: [
        {prop: 'fleetType', width: 150, label: '车队类型', align: 'center'},
        {prop: 'carCount', width: 120, label: '承包车辆数(台)', align: 'center'},
        {prop: 'premiumScale', width: 120, label: '年保费规模(万元)', align: 'center'},
        {prop: 'fleetTypeRate', width: 190, label: '车队类型占总保费用比(%)', align: 'center'},
        {prop: 'force', width: 70, label: '交强(%)', align: 'center'},
        {prop: 'lost', width: 120, label: '车损(含不计)(%)', align: 'center'},
        {prop: 'three', width: 120, label: '三者(含不计)(%)', align: 'center'},
        {prop: 'goods', width: 100, label: '车上货物(%)', align: 'center'},
        {prop: 'driver', width: 150, label: '车上人员(驾驶员)(%)', align: 'center'},
        {prop: 'passenger', width: 150, label: '车上人员(乘客)(%)', align: 'center'},
        {prop: 'glass', width: 70, label: '玻璃(%)', align: 'center'},
        {prop: 'thief', width: 120, label: '盗抢(含不计)(%)', align: 'center'},
        {prop: 'nature', width: 120, label: '自燃损失险(%)', align: 'center'},
        {prop: 'other', width: 110, label: '其他险别(%)', align: 'center'},
        {prop: 'total', width: 80, label: '合计 (%)', align: 'center'}
      ],
      showData: [
        {
          fleetType: '营业出租租赁客车',
          carCount: '200',
          premiumScale: '298.85',
          fleetTypeRate: '100.00',
          force: '21.13',
          lost: '32.9',
          three: '31.14',
          goods: '0',
          driver: '0.81',
          passenger: '0.5',
          glass: '2.67',
          thief: '7.6',
          nature: '2.93',
          other: '0.32',
          total: '100.00'
        }
      ],
      edits: ['serviceCost', 'exhibitionCost', 'awardUp'],
      editColumn: [
        {prop: 'insuranceType', width: 120, label: '险种', align: 'center'},
        {prop: 'fleetType', width: 150, label: '车队类型', align: 'center'},
        {prop: 'premiumPercent', width: 120, label: '保费占比(%)', align: 'center'},
        {prop: 'auto', width: 120, label: '自主系数', align: 'center'},
        {prop: 'historyAuto', width: 120, label: '历史自主系数', align: 'center'},
        {prop: 'serviceCost', width: 120, label: '手续费(%)', align: 'center'},
        {prop: 'exhibitionCost', width: 120, label: '展业费(%)', align: 'center'},
        {prop: 'awardUp', width: 120, label: '价值提奖(%)', align: 'center'},
        {prop: 'total', width: 120, label: '合计(%)', align: 'center'},
        {prop: 'approveMarketUp', width: 190, label: '已审批市场费用上限(%)', align: 'center'},
        {prop: 'historyFullPayRate', width: 140, label: '历史满期赔付率(%)', align: 'center'},
        {prop: 'finalPayRate', width: 120, label: '终极赔付率(%)', align: 'center'},
        {prop: 'operationCostRate', width: 120, label: '经营成本率(%)', align: 'center'}
      ],
      editData: [
        {
          insuranceType: '交强险',
          fleetType: '营业出租租赁客车',
          premiumPercent: '21.13',
          auto: '1',
          historyAuto: '1',
          serviceCost: '',
          exhibitionCost: '',
          awardUp: '',
          total: '',
          approveMarketUp: '0.00',
          historyFullPayRate: '',
          finalPayRate: '',
          operationCostRate: ''
        },
        {
          insuranceType: '商业险',
          fleetType: '营业出租租赁客车',
          premiumPercent: '78.87',
          auto: '1.45245252',
          historyAuto: '1.45245252',
          serviceCost: '',
          exhibitionCost: '',
          awardUp: '',
          total: '',
          approveMarketUp: '0.00',
          historyFullPayRate: '',
          finalPayRate: '',
          operationCostRate: ''
        },
        {
          insuranceType: '交强险合计',
          fleetType: '',
          premiumPercent: '21.13',
          auto: '1',
          historyAuto: '1',
          serviceCost: '0.00',
          exhibitionCost: '0.00',
          awardUp: '0.00',
          total: '0.00',
          approveMarketUp: '0.00',
          historyFullPayRate: '27.67',
          finalPayRate: '67.97',
          operationCostRate: '89.34'
        },
        {
          insuranceType: '商业险合计',
          fleetType: '',
          premiumPercent: '78.87',
          auto: '1.45245252',
          historyAuto: '1.45245252',
          serviceCost: '0.00',
          exhibitionCost: '0.00',
          awardUp: '0.00',
          total: '0.00',
          approveMarketUp: '0.00',
          historyFullPayRate: '27.67',
          finalPayRate: '67.97',
          operationCostRate: '89.34'
        }
      ],
      approve1: '',
      approve2: ''
    }
  },
  methods: {
    getSummaries (param) {
      const sums = []
      sums[0] = '合计'
      sums[1] = ''
      sums[2] = '100'
      sums[3] = ''
      sums[4] = ''
      sums[5] = '0.00'
      sums[6] = '0.00'
      sums[7] = '0.00'
      sums[8] = '0.00'
      sums[9] = ''
      sums[10] = '27.67'
      sums[11] = '67.97'
      sums[12] = '89.34'

      return sums
    },
    collect () {
      this.$router.push('/admin/fleet/declare/modify/collect')
    }
  }
}
</script>

<style scoped>
  .approval{
    width: 95%;
    margin: 0 auto;
  }

  .approval > .result > .title{
    text-align: center;
    height: 30px;
    line-height: 30px;
    background-color: #8CA5FF;
  }

</style>



